import * as React from "react";
import { Link } from "react-router-dom";
import { contactPath, rootPath, selfIntroPath } from "../routes";
import product1 from "../styles/images/product1.png";
import product2 from "../styles/images/product2.png";

/* tslint:disable */
export class Product extends React.Component {
  public componentDidMount() {
    window.scrollTo(0, 0);
  }

  public render() {
    return (
      <div>
        <div className="pd-Content">
          <div className="pd-Content_title"></div>
          <div className="pd-Content_bar" />
          <div className="pd-Content_description">
            <div className="pd-Content_description-text">
              <h2></h2>
               React + TypeScript + Phoenix(Elixir) <br />
               SPAPWA <br />
               APIGraphQLApolloClient <br />
               CSS <br />
               <a href="https://github.com/getty104/getty104.github.io" target="_blank"></a> <br />
               <a href="https://github.com/getty104/blog_app_ex" target="_blank"></a>
            </div>
            <a href="https://www.getty104.tk" target="_blank" className="pd-Content_description-image"><img src={product1} /></a>
          </div>
          <div className="pd-Content_description">
            <div className="pd-Content_description-text">
              <h2>Life Squad</h2>
               Rails + React + TypeScript <br />
                <br />
               
            </div>
            <a href="https://www.life-squad.jp" target="_blank" className="pd-Content_description-image"><img src={product2}/></a>
          </div>
          <div className="pd-Content_description">
            <div className="pd-Content_description-other">
              <h2></h2>
                Ruby, TypeScript, SQL, Elixir, Java, Solidity, python3, C++ <br />
                 Rails, React, hyperapp, Phoenix <br />
                 BigQuery, Docker, Vagrant, GraphQL, ServiceWorker <br />
                
            </div>
          </div>
        </div>
        <div className="pd-Footer">
          <Link to={selfIntroPath()} className="pd-Footer_column"></Link>
          <Link to={rootPath()} className="pd-Footer_column"></Link>
          <Link to={contactPath()} className="pd-Footer_column"></Link>
        </div>
      </div>
    );
  }
}



